@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

button {
  cursor: pointer;
}

.container {
  background-color: #eaeccc;
}

.nav {
  color: #e09680;
  padding: 1rem 4rem;
  font-size: 2rem;
  font-weight: bold;

  border-bottom: 1px solid rgba(0, 0, 0, 0.684);

  display: flex;
  justify-content: space-between;
}

.nav button {
  background-color: none;
  border: none;
  padding: 0.5rem;
  font-size: 1.2rem;
  cursor: pointer;
}

.project-section,
.task-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 3rem;
  margin-bottom: 5rem;
}

.task-section {
    margin: 0;
}

.project-create-form {
  margin: auto;
  background-color: rgba(101, 123, 235, 0.526);
  max-width: 45rem;
  padding: 1rem 4rem;
  border: 1px solid rgb(101, 123, 235);
  border-radius: 5px;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  gap: 1rem;
}

.form-header h2 {
  font-size: 20px;
}

.form-header h4 {
  font-size: 12px;
  opacity: 0.8;
}

.form-inputs {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.form-inputs input {
  border: none;
  background-color: rgb(214, 214, 214);
  border-radius: 5px;
  padding: 0.5rem;
}

.form-inputs input:focus {
  background-color: white;
}

.form-btn {
  padding: 0.5rem;
  border: none;
  border-radius: 6px;
  font-size: 0.9rem;
  color: white;
  background-color: #4c3575;
}

.projects-container {
  margin-top: 2rem;

  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.projects-container h2 {
  padding-bottom: 0.5rem;
  border-bottom: 1px solid black;
}

.project-list {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.project {
  background-color: aliceblue;
  padding: 1rem 2rem;
  border: 1px solid black;
  border-radius: 10px;
  cursor: pointer;

  display: flex;
  justify-content: space-between;
  align-items: center;
}

.project:hover {
  box-shadow: 0 2px 2px 3px rgb(0 0 0 / 0.2);
  background-color: rgba(240, 248, 255, 0.733);
  transition: 0.4s;
}

.project:hover:active {
  box-shadow: 0 2px 2px 3px rgb(0 0 0 / 0.4);
  transform: translateY(2px);
}

.project button {
  border: none;
  border-radius: 5px;
  background-color: #4c3575;
  color: white;
  padding: 0.5rem;
}

.project button:hover {
  transition: 0.5s;
  background-color: #4c3575b8;
}

.task-header {
  padding-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid rgba(0, 0, 0, 0.693);
}

.create-task-btn {
  border: none;
  border-radius: 5px;
  padding: 0.5rem 1rem;

  display: flex;
  align-items: center;
  gap: 0.5rem;

  color: white;
  background-color: #4c3575;
}

.create-task-btn span {
  width: 20px;
  height: 20px;
  border-radius: 100%;
  background-color: white;
  color: #4c3575;
  font-size: 1rem;
  line-height: 1.2;
  font-weight: bold;
}

.create-task-btn:hover {
  transition: 0.5s;
  background-color: #4f3974a9;
}

.user-container {
  margin: 1rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-weight: bold;
}

.user-container .user-img {
  width: 40px;
  border-radius: 50%;
}

.modal-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.modal-form .input-container {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.input-container .date-container {
  display: flex;
  gap: 0.5rem;
}

.input-container .date-container input {
  padding: 0.2rem;
  border-radius: 5px;
}

.user-selected-info-container {
  padding: 0.5rem;
  border: 1px solid rgba(0, 0, 0, 0.458);
  border-radius: 5px;

  display: flex;
  flex-direction: column;
  gap: 1rem;
}
